.navbar_container{
    display: flex;
    flex-direction: row;
    padding-left: 20px;
    padding-top: 20px;
    padding-bottom: 20px;
    font-size: 30px;
  }

  
  .links {
    width: 100%;
    display: flex;
    justify-content: flex-end;
  }
  
  .navbar_container a {
    transition: ease-in 200ms;
    text-decoration: none;
    color: #ffffff;
    padding-right: 20px;
  }

  .navbar_container a:hover{
    color: #ffffff;
    text-shadow: 3px 3px 5px #f952fc;
  }